import React from 'react';

/**
 *  父类调用子类的办法 在子类上添加onRef属性 传递函数给子组件 
 *  子组件在回调方法中把this传给parent component
 *  onRef={(ref) => { this.child = ref; }}
 *         if (this.props.onRef) {
            this.props.onRef(this);
         }

         alert(this.child.state.sonmsg);
         this.child.run();
    
 */



class Node1 extends React.Component {

    componentDidMount() {
        console.log('Node1 componentDidMount');
    }

    render() {
        return (
            <>
                <div>node1</div>
                <Node2 />
            </>
        )
    }
}

class Node2 extends React.Component {

    componentDidMount() {
        console.log('Node2 componentDidMount');
    }


    render() {
        return (
            <>
                <div>node2</div>
                <Node3 />
            </>
        )
    }
}

class Node3 extends React.Component {

    componentDidMount() {
        console.log('Node3 componentDidMount');
    }


    render() {
        return <div>node3</div>
    }
}



export class Son extends React.Component {
    // 类变量
    static ssname = 'sssss';

    constructor(props) {
        super(props);
        this.state = { 'childName': 'zzzzz' };
        // 实例变量
        this.name = 123;
    };

    componentDidMount() {

        console.log('Son componentDidMount');
        if (this.props.onRef) {
            this.props.onRef(this);
        }
    }

    run = () => {
        console.log('child run~~~~~')
    }

    render() {
        return (
            <>
                <Node1 />
                <div>I am child</div>
            </>
        );
    }
}


class Father extends React.Component {
    constructor(props) {
        super(props);
        this.state = { 'name': "jojo" }
    }

    componentDidMount() {

        console.log('Father componentDidMount');
    };

    componentDidUpdate() {

    };

    showChildRun = () => {
        console.log(this.child);
        this.child.run();
        console.log(this.child.state);
        console.log(Son.ssname);
    }


    render() {
        return (
            <>
                <Son onRef={(ref) => { this.child = ref; }} />
                <div>I am Parent and my name is {this.state.name}</div>
                <button onClick={this.showChildRun}>click...</button>
            </>
        )
    }

}

export default Father;