import React, { useState, useEffect, useRef } from 'react'

function GrandSon(){


    const [name, setName] = useState("");

    useEffect(()=>{
        console.log('====grandSon=====', name)
    })

    const onInputChange = (e)=>{
        setName(e.target.value)
    }

    return (<div style={{border:'1px solid blue'}}>
      {console.log('render GrandSon')}
    <b>GrandSon</b>
    <input type="text" onChange={onInputChange} />
    </div>)
}


function Son() {
    // 声明一个新的叫做 “count” 的 state 变量
    const [count, setCount] = useState(0);


    const fooRef = useRef(null);
    const buttonRef = useRef(null)

    useEffect(() => {
        console.log('====Son====');
        // console.log('====buttonRef==', buttonRef.current.innerHTML)
    },[count])

    return (
        <div style={{border:'2px solid green'}}>
                 {console.log('render Son')}
            <GrandSon />
            <p ref={fooRef}>You clicked {count} times</p>
            <button ref={buttonRef} onClick={() => setCount(count + 1)}>
                Click me <b>Son</b>
        </button>
        </div>
    );
}


const LoginDemo = React.memo( (props)=> {

    const [userName, setUserName] = useState("");
    const [password, setPassword] = useState("");


    useEffect(() => {
        console.log('====Root==')
        document.title = userName;
    },[userName, password])

    const onSubmitHandler = (e) => {
        e.preventDefault();
        console.log(userName, password);
    }

    const onUserChange = (e) => {
        setUserName(e.target.value);
    }

    const onPasswordChange = (e) => {
        setPassword(e.target.value);
    }

    function test() {
        console.log('ttttt');
    }

    return (
        <div style={{
            border: '3px solid red',
            width: 400, height: 500, margin: 'auto'
        }}>
                    {console.log('render Root')}
            <Son />
             {test()}
            <h1>兴沪e家 = {password}</h1>
            <h3>打造“网上职工之家”，开启工会服务新局面</h3>
            <form action="baidu.com" onSubmit={onSubmitHandler}>
                户名: <input value={userName} onChange={onUserChange} 
                type="text" placeholder="pls input username" />
                <br /><br />
                密码: <input type="password" onChange={onPasswordChange} value={password} />
                <br /><br />
                <input type="submit" value="登录" />
            </form>

            
        </div>);
});


export default LoginDemo;